.background {
    position: relative;

    min-width: 483px;

    display: flex;

    width: auto;
    height: 50px;

    background-color:navajowhite;
    overflow: hidden;

    border-top: 1px solid gray;
    border-bottom: 1px solid gray;

    z-index: 10000;
  }

  .topnav{
    display: flex;
    max-width: 1200px;

    width: 100%;
    height: 100%;

    align-items:center;

    margin: 0 auto 0 auto;
  }
  
  .topnav a {
    display: flex;
    color:black;
    
    text-decoration: none;
    font-size: 17px;
  }
  
  .topnav a.leftAtt{
    margin-left: 30px;
  }

  .topnav_icon{
    display: flex;

    margin-left: auto;
    align-items: center;

    list-style: none;

    margin-right: 20px;
  }
  
  .topnav a.Youtube{
    color: #f31515;
    
  }
  
  .topnav a.Instagram{  
  }

  .topnav img{ 
    width: 40px;
    height: 36px;


  }
  
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .topnav a.active {
    background-color: #4CAF50;
    color: white;
  }

